<template>
  <div class="container">
    <!-- 设置返回按钮 START-->
    <div class="header">
      <div><img :src="back_img" alt="返回" class="back" @click="back"></div>
      <span>{{anotherName}}</span>
    </div>
    <!-- 设置返回按钮 END -->
    <div v-for="(recording, index) in recordingList" class="recording_header" :key="index">
      <div class="recording_top" v-if="recording.theRecipient != another">
        <div class="time_line"><span>{{recording.sendTime  | dateFm}}</span></div>
        <div class="recording_content">
          <img :src="recording.sendUserIdAvatar" alt="头像">
          <span>{{recording.noteContent}}</span>
        </div>
      </div>
      <div class="recording_top" v-if="recording.theRecipient == another">
        <div class="time_line"><span>{{recording.sendTime  | dateFm}}</span></div>
        <div class="recording_content_right">
          <span>{{recording.noteContent}}</span>
          <img :src="recording.sendUserIdAvatar" alt="头像">
        </div>
      </div>
    </div>
    <img class="send_icon" @click="sendNews" :src="sendImg" alt="发送信息">
  </div>
</template>
<script>
  import headerNavBack from '@/components/headerNavBack'
  import { createNamespacedHelpers } from "vuex";
  const { mapActions, mapState } = createNamespacedHelpers(
    "newsModule"
  );
  export default {
    components: {
      headerNavBack
    },
    data() {
      return {
        // 接收消息对话集合
        recordingList: new Array(),
        // 对话者
        another: null,
        // 对话昵称
        anotherName: null,
        // 接收ID
        receive: null,
        // 发送ICON
        sendImg: require('../../../assets/img/sent_page.png'),
        // 返回ICON
        back_img: require('../../../assets/img/per_fh.png')
      }
    },
    mounted() {
      this.toBottom()
      this.another = this.$route.params.anotherUserId
      this.getBetweenNewsInfo()
    },
    methods: {
      ...mapActions([
        'BETWEEN_TIDINGS'
      ]),
      // 得到对话信息列表
      getBetweenNewsInfo() {
        this.BETWEEN_TIDINGS({
          anotherUserId: this.$route.params.anotherUserId
        }).then(data => {
          this.recordingList = data.data.result.list.reverse();
          if (this.recordingList && this.recordingList.length > 0) {
            if (data.data.result.list[0].sendUserId == this.$store.state.user.user.userId) {
              this.anotherName = data.data.result.list[0].theRecipientNickname;
              this.receive = data.data.result.list[0].theRecipient;
            } else {
              this.anotherName = data.data.result.list[0].sendUserNickname;
              this.receive = data.data.result.list[0].sendUserId;
            }
          }
        })
      },
      // 滑动至底部
      toBottom() {
        document.scrollingElement.scrollTop = document.scrollingElement.scrollHeight;
      },
      // 返回上一步
      back() {
        this.$router.back(-1);
      },
      // 进入发消息页面
      sendNews() {
        this.$router.push({
          name: 'sendNews',
          params: {
            id: this.receive,
            name: this.anotherName
          }
        });
      }
    }
  }
</script>
<style lang="less" scoped>
  /* 我的消息 返回按钮 START */
  .header {
    height: 90px;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 1;
    border-bottom: .0625rem solid rgba(0, 0, 0, .1);
    div {
      padding-left: 20px;
      height: 2.5rem;
      vertical-align: middle;
      img {
        width: 30px;
        vertical-align: middle;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    span {
      vertical-align: middle;
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 30px;
    }
  } // 头部
  /* 我的消息 返回按钮 END */
  .recording_top {
    margin-top: 20px;
    height: 100%;
  }
  .recording_header {
    margin-top: 20px;
  }
  .time_line {
    width: 100%;
    text-align: center;
    clear: both;
    &>span:nth-child(1) {
      display: inline-block;
      width: 300px;
      height: 40px;
      background-color: rgba(0, 0, 0, .2);
      color: #fff;
      border-radius: 20px;
      font-size: 20px;
      line-height: 40px;
    }
  }
  .recording_content {
    margin-top: 20px;
    &>img {
      width: 100px;
      height: 100px;
      vertical-align: middle;
      border-radius: 50%;
      margin-left: 10px;
    }
    &>span {
      margin-left: 10px;
      display: inline-block;
      vertical-align: middle;
      font-size: 25px;
      padding:20px 40px;
      max-width: 300px;
      box-sizing: border-box;
      border-radius: 20px;
      position: relative;
      background-color: #fff;
      left: 10px;
      &::after {
        position: absolute;
        content: '';
        top: 50%;
        left: -16px;
        transform: translateY(-50%);
        display: block;
        width: 0;
        height: 0;
        border-top: 16px solid transparent;
        border-bottom: 16px solid transparent;
        border-right: 16px solid #fff;
      }
    }
  }
  .recording_content_right {
    margin-top: 20px;
    height: 100%;
    float: right;
    &>img {
      width: 100px;
      height: 100px;
      vertical-align: middle;
      border-radius: 50%;
      margin-right: 10px;
    }
    &>span {
      margin-right: 10px;
      display: inline-block;
      vertical-align: middle;
      font-size: 25px;
      background-color: #ffad4a;
      color: #fff;
      padding: 20px 40px;
      max-width: 300px;
      box-sizing: border-box;
      border-radius: 20px;
      position: relative;
      left: -10px;
      &::after {
        position: absolute;
        content: '';
        top: 50%;
        right: -16px;
        transform: translateY(-50%);
        display: block;
        width: 0;
        height: 0;
        border-top: 16px solid transparent;
        border-bottom:  16px solid transparent;
        border-left:  16px solid #ffad4a;
      }
    }
  }
  .send_icon {
    position: fixed;
    bottom: 40px;
    right: 40px;
    width: 150px;
    height: 150px;
  }
</style>




